body {
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
}

.header__container {
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__logo {
  width: 40px;
  height: 40px;
}

.header__logo-png {
  width: 100%;
  height: auto;
  display: block;
}

.navigation__container {
  flex-direction: row;
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.navigation__container-list {
  text-transform: uppercase;
  margin-right: 20px;
  font-weight: 500;
  line-height: normal;
}

.navigation__container-list:last-child {
  margin: 0;
}

.navigation__container-list-link {
  color: #060b35;
  line-height: 60px;
  text-decoration: none;
  display: block;
}

.navigation__container-list-link:hover {
  color: #00acdc;
}

.is-active {
  color: #00acdc;
  position: relative;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

html, h1, h2, p {
  box-sizing: border-box;
  font-family: Roboto, sans-serif;
  font-size: 10px;
  font-style: normal;
  font-weight: 400;
  line-height: 14px;
}

.cards {
  box-sizing: border-box;
  grid-template-columns: repeat(4, 200px);
  justify-content: center;
  align-items: center;
  gap: 46px 48px;
  margin: 0 auto;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .cards .cards {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .cards .cards {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .cards .cards {
    grid-template-columns: repeat(4, 200px);
  }
}

.card {
  box-sizing: border-box;
  z-index: 0;
  background: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  justify-content: center;
  width: 200px;
  padding: 32px 16px 16px;
  transition: transform .3s ease-in-out, box-shadow .3s ease-in-out;
  display: flex;
  position: relative;
}

.card:hover {
  transform: scale(1.2);
}

.card__product {
  flex-direction: column;
  justify-content: center;
  display: flex;
}

.card__product-image {
  flex-shrink: 0;
  justify-content: center;
  width: 160px;
  height: 134px;
  margin: 0 auto;
}

.card__product-name {
  text-align: left;
  color: #060b35;
  margin: 40px 0 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
  display: flex;
}

.card__product-name:hover {
  color: #34568b;
}

.card__product-code {
  color: #616070;
  justify-content: flex-start;
  margin: 4px 0 0;
  display: flex;
}

.card__info {
  flex-direction: column;
  justify-content: center;
  display: flex;
}

.card__info-reviews {
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-end;
  margin-top: 16px;
  display: flex;
}

.card__info-reviews-amount {
  text-align: right;
  color: #060b35;
  justify-content: flex-end;
  align-items: center;
  margin: 0;
  display: flex;
}

.card__info-price {
  flex-direction: row;
  justify-content: space-between;
  margin: 24px 0 16px;
  display: flex;
}

.card__info-price-tag {
  color: #616070;
  justify-content: center;
  align-items: center;
  font-size: 12px;
  line-height: 18px;
  display: flex;
}

.card__info-price-number {
  text-align: right;
  color: #060b35;
  margin: 0;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
  display: flex;
}

.card__info-button {
  color: #fff;
  text-transform: uppercase;
  background-color: #00acdc;
  border: 0;
  border-radius: 5px;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  display: flex;
}

.card__info-button:hover {
  box-sizing: border-box;
  color: #00acdc;
  text-transform: uppercase;
  background: #fff;
  border: 1px solid #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
  display: flex;
}

.stars__star:last-child {
  margin-right: 0;
}

.stars--1 .stars__star:nth-child(-n+1), .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}
